.Toolbar {
  height: 5.6rem;
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
  background-color: #703b09;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-align: center;
      align-items: center;
  padding: 0 2rem;
  z-index: 1;
}

.Toolbar nav {
  height: 100%;
}

.Logo {
  height: 80%;
}

@media only screen and (max-width: 499px) {
  .DesktopOnly {
    display: none;
  }
}



